// src/routes/users.js
const express = require('express');
const bcrypt = require('bcryptjs');
const db = require('../db');

const router = express.Router();

// üß© ÿØÿßŸÑÿ© ÿ™ÿ±ÿ®ÿ∑ ÿßŸÑÿµŸÑÿßÿ≠Ÿäÿßÿ™ ŸÖÿπ ÿßŸÑŸÖÿ≥ÿ™ÿÆÿØŸÖŸäŸÜ
async function attachPermissionsToUsers(users) {
  if (!users || !users.length) return users;

  const ids = users.map(u => u.id);
  const placeholders = ids.map(() => '?').join(',');
  const [rows] = await db.query(
    `SELECT user_id, permission_key 
     FROM user_permissions 
     WHERE user_id IN (${placeholders})`,
    ids
  );

  const map = {};
  rows.forEach(row => {
    if (!map[row.user_id]) map[row.user_id] = [];
    map[row.user_id].push(row.permission_key);
  });

  return users.map(u => ({
    ...u,
    permissions: map[u.id] || [],
  }));
}

// üü¢ ÿ¨ŸÑÿ® ÿßŸÑŸÖÿ≥ÿ™ÿÆÿØŸÖŸäŸÜ
router.get('/', async (req, res) => {
  try {
    const [rows] = await db.query(
      'SELECT id, name, username, email, role, active, created_at FROM users ORDER BY id ASC'
    );

    const usersWithPerms = await attachPermissionsToUsers(rows);
    res.json(usersWithPerms);
  } catch (err) {
    console.error('GET /users error:', err);
    res.status(500).json({ message: 'ÿÆÿ∑ÿ£ ŸÅŸä ÿ¨ŸÑÿ® ÿßŸÑŸÖÿ≥ÿ™ÿÆÿØŸÖŸäŸÜ' });
  }
});

// ‚ûï ÿ•ÿ∂ÿßŸÅÿ© ŸÖÿ≥ÿ™ÿÆÿØŸÖ
router.post('/', async (req, res) => {
  try {
    const { name, username, email, password, role } = req.body;

    if (!name || !username || !password) {
      return res.status(400).json({ message: 'ÿßŸÑÿ≠ŸÇŸàŸÑ ÿßŸÑÿ£ÿ≥ÿßÿ≥Ÿäÿ© ŸÖÿ∑ŸÑŸàÿ®ÿ©' });
    }

    const safeRole = ['admin', 'pharmacist', 'cashier'].includes(role)
      ? role
      : 'cashier';

    // ÿßŸÑÿ™ÿ£ŸÉÿØ ŸÖŸÜ ÿπÿØŸÖ ÿ™ŸÉÿ±ÿßÿ± ÿßÿ≥ŸÖ ÿßŸÑŸÖÿ≥ÿ™ÿÆÿØŸÖ
    const [existing] = await db.query(
      'SELECT id FROM users WHERE username = ? LIMIT 1',
      [username]
    );
    if (existing.length) {
      return res
        .status(409)
        .json({ message: 'ÿßÿ≥ŸÖ ÿßŸÑŸÖÿ≥ÿ™ÿÆÿØŸÖ ŸÖÿ≥ÿ™ÿÆÿØŸÖ ŸÖÿ≥ÿ®ŸÇŸãÿß' });
    }

    const passwordHash = await bcrypt.hash(password, 10);

    // ŸÖŸÑÿßÿ≠ÿ∏ÿ©: ŸÜÿÆÿ≤ŸÜ ÿßŸÑÿ®ÿßÿ≥Ÿàÿ±ÿØ ÿßŸÑŸÖÿ¥ŸÅŸëÿ± ŸÅŸä ÿπŸÖŸàÿØ "password"
    const [result] = await db.query(
      `INSERT INTO users (name, username, email, password, role, active)
       VALUES (?,?,?,?,?,1)`,
      [name, username, email || null, passwordHash, safeRole]
    );

    const [rows] = await db.query(
      'SELECT id, name, username, email, role, active, created_at FROM users WHERE id = ?',
      [result.insertId]
    );

    const [userWithPerms] = await attachPermissionsToUsers(rows);
    res.status(201).json(userWithPerms);
  } catch (err) {
    console.error('POST /users error:', err);
    res.status(500).json({ message: 'ÿÆÿ∑ÿ£ ŸÅŸä ÿ•ŸÜÿ¥ÿßÿ° ÿßŸÑŸÖÿ≥ÿ™ÿÆÿØŸÖ' });
  }
});

// üîÑ ÿ™ŸÅÿπŸäŸÑ / ÿ™ÿπÿ∑ŸäŸÑ ŸÖÿ≥ÿ™ÿÆÿØŸÖ
router.patch('/:id/toggle', async (req, res) => {
  try {
    const userId = parseInt(req.params.id, 10);

    const [rows] = await db.query(
      'SELECT active FROM users WHERE id = ?',
      [userId]
    );
    if (!rows.length) {
      return res.status(404).json({ message: 'ÿßŸÑŸÖÿ≥ÿ™ÿÆÿØŸÖ ÿ∫Ÿäÿ± ŸÖŸàÿ¨ŸàÿØ' });
    }

    const current = rows[0].active ? 1 : 0;
    const next = current ? 0 : 1;

    await db.query(
      'UPDATE users SET active = ? WHERE id = ?',
      [next, userId]
    );

    res.json({ id: userId, active: !!next });
  } catch (err) {
    console.error('PATCH /users/:id/toggle error:', err);
    res.status(500).json({ message: 'ÿÆÿ∑ÿ£ ŸÅŸä ÿ™ÿ≠ÿØŸäÿ´ ÿ≠ÿßŸÑÿ© ÿßŸÑŸÖÿ≥ÿ™ÿÆÿØŸÖ' });
  }
});

// üóëÔ∏è ÿ≠ÿ∞ŸÅ ŸÖÿ≥ÿ™ÿÆÿØŸÖ
router.delete('/:id', async (req, res) => {
  try {
    const userId = parseInt(req.params.id, 10);

    await db.query('DELETE FROM user_permissions WHERE user_id = ?', [userId]);
    await db.query('DELETE FROM users WHERE id = ?', [userId]);

    res.json({ message: 'ÿ™ŸÖ ÿ≠ÿ∞ŸÅ ÿßŸÑŸÖÿ≥ÿ™ÿÆÿØŸÖ' });
  } catch (err) {
    console.error('DELETE /users/:id error:', err);
    res.status(500).json({ message: 'ÿÆÿ∑ÿ£ ŸÅŸä ÿ≠ÿ∞ŸÅ ÿßŸÑŸÖÿ≥ÿ™ÿÆÿØŸÖ' });
  }
});

// üì• ÿ¨ŸÑÿ® ÿµŸÑÿßÿ≠Ÿäÿßÿ™ ŸÖÿ≥ÿ™ÿÆÿØŸÖ Ÿàÿßÿ≠ÿØ (ÿßÿÆÿ™Ÿäÿßÿ±Ÿä ŸÑŸà ÿßÿ≠ÿ™ÿ¨ÿ™Ÿá)
router.get('/:id/permissions', async (req, res) => {
  try {
    const userId = parseInt(req.params.id, 10);

    const [rows] = await db.query(
      'SELECT permission_key FROM user_permissions WHERE user_id = ?',
      [userId]
    );

    const perms = rows.map(r => r.permission_key);
    res.json({ userId, permissions: perms });
  } catch (err) {
    console.error('GET /users/:id/permissions error:', err);
    res.status(500).json({ message: 'ÿÆÿ∑ÿ£ ŸÅŸä ÿ¨ŸÑÿ® ÿßŸÑÿµŸÑÿßÿ≠Ÿäÿßÿ™' });
  }
});

// üíæ ÿ≠ŸÅÿ∏ ÿßŸÑÿµŸÑÿßÿ≠Ÿäÿßÿ™
router.put('/:id/permissions', async (req, res) => {
  try {
    const userId = parseInt(req.params.id, 10);
    const { permissions } = req.body;

    if (!Array.isArray(permissions)) {
      return res
        .status(400)
        .json({ message: 'ÿµŸäÿ∫ÿ© ÿßŸÑÿµŸÑÿßÿ≠Ÿäÿßÿ™ ÿ∫Ÿäÿ± ÿµÿ≠Ÿäÿ≠ÿ©' });
    }

    // ŸÜÿ≠ÿ∞ŸÅ ÿßŸÑÿµŸÑÿßÿ≠Ÿäÿßÿ™ ÿßŸÑŸÇÿØŸäŸÖÿ©
    await db.query('DELETE FROM user_permissions WHERE user_id = ?', [
      userId,
    ]);

    // ŸÑŸà ŸÑÿß ÿ™Ÿàÿ¨ÿØ ÿµŸÑÿßÿ≠Ÿäÿßÿ™ ÿ¨ÿØŸäÿØÿ© ‚Üí ŸÜÿ±ÿ¨ÿπ ÿ®ŸÜÿ¨ÿßÿ≠
    if (!permissions.length) {
      return res.json({ message: 'ÿ™ŸÖ ÿ≠ŸÅÿ∏ ÿßŸÑÿµŸÑÿßÿ≠Ÿäÿßÿ™ (ÿ®ÿØŸàŸÜ ÿ£Ÿä ÿµŸÑÿßÿ≠Ÿäÿßÿ™)' });
    }

    // ŸÜÿ¨Ÿáÿ≤ ÿßŸÑŸÇŸäŸÖ ŸÑŸÑÿ•ÿØÿÆÿßŸÑ ÿßŸÑŸÖÿ™ÿπÿØÿØ
    const values = permissions.map(p => [userId, p]);
    const placeholders = values.map(() => '(?, ?)').join(' ');
    const flat = values.flat();

    await db.query(
      `INSERT INTO user_permissions (user_id, permission_key) VALUES ${placeholders}`,
      flat
    );

    res.json({ message: 'ÿ™ŸÖ ÿ≠ŸÅÿ∏ ÿßŸÑÿµŸÑÿßÿ≠Ÿäÿßÿ™' });
  } catch (err) {
    console.error('PUT /users/:id/permissions error:', err);
    res.status(500).json({ message: 'ÿÆÿ∑ÿ£ ŸÅŸä ÿ≠ŸÅÿ∏ ÿßŸÑÿµŸÑÿßÿ≠Ÿäÿßÿ™' });
  }
});

module.exports = router;
















